                                {/* <h1 className='text-gray-600 text-md'>click on button to re-upload document</h1> */}
                                <button type="button" id={items?.id} value='Active' class="float-right text-xs shadow-lg mr-2 rounded leading-5 border-gray-50 bg-indigo-500 px-2 py-1 text-white" onClick={(e) => docUploadModal(items?.id, 'agency')}>upload Document</button>
